<template>
  <div
    id="app"
    class="mousearea"
    @mouseenter="onmouseenter"
    @mouseleave="onmouseleave"
    @mousewheel="onmousewheel"
    @mousemove="onmousemove"
    @mousedown="onmousedown"
    @mouseup="onmouseup"
  >
    <!-- 仅供测试，可以删除 -->
    <div
      class="testbox"
      style="
        width: 300px;
        height: 250px;
        background: white;
        position: absolute;
        right: 20px;
        top: 20px;
        padding: 20px;
      "
    >
      发送消息<br />
      <textarea v-model="msgText" style="width: 100%; height: 60px" /><br />
      <button @click="sendMsg">发送</button><br /><br />

      接收消息<br />
      <textarea v-model="recvMsg" readonly style="width: 100%; height: 60px" />
    </div>

    <!-- 测试面板 -->
    <div class="testleft">
      <div>
        摄像头控制<br />
        <div v-for="(cameraId, index) in cameras" v-bind:key="cameraId">
          摄像头{{ index + 1 }}
          <button @click="moveCameraPod(cameraId)">定位</button>
          <button @click="changeDeviceStatus(cameraId, 'show')">显示</button>
          <button @click="changeDeviceStatus(cameraId, 'hidden')">隐藏</button
          ><br />
        </div>
      </div>
      <div>
        门控制<br />
        <div v-for="(doorId, index) in doors" v-bind:key="doorId">
          门{{ index + 1 }}
          <button @click="moveDoorPos(doorId)">定位</button>
          <button @click="changeDoorStatus(doorId, 'open')">开门</button>
          <button @click="changeDoorStatus(doorId, 'close')">关门</button><br />
        </div>
      </div>
      <div>
        天气控制<br />
        <!-- 天气以及时间  -->

        0点 <input type="range" value="0" min="0" max="23" @change="timeWeather" v-model="time"> 23点
        <br>

        <button
          v-for="index in 4"
          v-bind:key="index * 100"
          @click="weather(index)"
          v-bind:style="{
            backgroundColor: index === selectWeather ? 'grey' : 'buttonface',
          }"
        >
          {{ weathers[index].name }}
        </button>
      </div>
      <div>
        建筑定位以及炸开<br />
        <div v-for="item in builds" v-bind:key="item.name">
          <button @click="moveBuild(item.id, 3)">{{ item.name }}</button>
          <button
            v-for="floor in item.floors"
            v-bind:key="floor * 10000"
            @click="moveBuild(item.id, floor)"
          >
            楼层{{ floor }}
          </button>
        </div>
      </div>
      <div>
        倾斜摄影<br />
        <button @click="qxsy(1)">进入倾斜摄影</button>
        <button @click="qxsy(0)">退出倾斜摄影</button>
      </div>

      <!-- <div>
        轨迹线（随机顺序点击摄像头后点击连线） <br/>
        <button v-for="(cameraId, index) in cameras" v-bind:key="cameraId" @click="odlines.push({cameraId:cameraId,index:index})">
          摄像头{{index+1}}
        </button><br>
        <div style="font-size:12px">
          <span v-for="(item,index) in odlines" v-bind:key="index">
            摄像头{{item.index + 1}}
            <span v-if="index !== odlines.length - 1"> ->  </span>
          </span>
        </div>
        <button>连线</button><button @click="odlines=[]">清除</button>
      </div> -->

      <div>
        轨迹线 <br />
        <button @click="showPathLine(true)">显示</button>
        <button @click="showPathLine(false)">关闭</button>
      </div>
      <!-- div>
        获取三维视角<br />
        <button @click="getCurView">获取当前三维视角</button>
        <button
          v-for="(item, index) in views"
          v-bind:key="index * 1000"
          @click="viewSwitch(item)"
        >
          视角{{ index }}
        </button>
      </div -->
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      msgText: "",
      disabled: true,
      recvMsg: "",
      selectTime: 0,
      selectWeather: 1,
      time: 0,
      cameras: [
        "76ebf723-1498-1f52-ffb8-5f1d8a4ed01a",
        "bb96d339-17ed-ca4d-8c9c-7eba84e37d4e",
        "235b8e61-4520-6088-fd33-d7d5eada0276",
        "590b2088-d1fa-1a6c-6728-8d90103f69bd",
      ],
      doors: ["Door01", "Door02"],
      weathers: [
        {},
        {
          name: "多云",
          value: "cloud",
        },
        {
          name: "雨",
          value: "rain",
        },
        {
          name: "雪",
          value: "snow",
        },
        {
          name: "雾",
          value: "fog",
        },
      ],
      builds: [
        {
          name: "A3",
          id: "A3",
          floors: [0, 1, 2],
        },
        {
          name: "A4",
          id: "A4",
          floors: [0, 1, 2],
        },
      ],
      odlines: [],
      views: [],
    };
  },
  mounted() {
    window.addPsConnectSuccessListener(() => {
      this.disabled = false;
      window.addResponseEventListener("test", (data) => {
        this.recvMsg = data;

        try {
          let msg = JSON.parse(data);
          let type = msg.type || msg.Type;
          if (type === "3DElement") {
            if (msg.device_type === "Camera") {
              alert(`你点击了Id为${msg.id}的监控摄像头`);
            }
          }

          if (type === "GetViewResult") {
            this.views.push(msg.content);
          }

          if (type === "3DBuildingClicked") {
            alert(`你点击了id为${msg.id} 的建筑`);
          }
        } catch (e) {
          console.log(e);
        }
      });
    });
  },
  destroyed() {
    window.removeResponseEventListener("test");
  },
  methods: {
    sendMsg() {
      window.emitUIInteraction(this.msgText);
    },
    showPathLine(status) {
      window.emitUIInteraction({
        type: "PathLine",
        id: "line01",
        show: status,
      });
    },
    moveCameraPod(deviceId) {
      window.emitUIInteraction({
        type: "2DElement",
        device_type: "Camera",
        id: deviceId,
      });
    },
    moveBuild(id, floor) {
      window.emitUIInteraction({
        type: "2DBuildingClicked",
        id: id,
        floor: floor,
      });
    },
    moveDoorPos(doorId) {
      window.emitUIInteraction({
        type: "2DElement",
        device_type: "Door",
        id: doorId,
      });
    },
    changeDoorStatus(doorId, status) {
      window.emitUIInteraction({
        type: "DoorState",
        id: doorId,
        Open: status === "open",
      });
    },
    changeDeviceStatus(deviceId, status) {
      window.emitUIInteraction({
        type: "2DDeviceState",
        id: deviceId,
        Show: status === "show",
      });
    },
    timeWeather() {
      window.emitUIInteraction({
        type: "Time",
        content: parseInt(this.time),
      });
    },
    weather(index) {
      window.emitUIInteraction({
        type: "Weather",
        content: this.weathers[index].value,
      });
      this.selectWeather = index;
    },
    getCurView() {
      window.emitUIInteraction({
        type: "GetView",
      });
    },
    viewSwitch(item) {
      window.emitUIInteraction({
        type: "ViewSwitch",
        content: item,
      });
    },
    qxsy(status) {
      window.emitUIInteraction({
        type: "QXSYState",
        status: status,
      });
    },
    // 下面的代码不能删除
    filterEvent(event, callback) {
      let className = event.path[0].getAttribute("class") || "";
      if (className.indexOf("mousearea") !== -1) {
        callback(event);
      }
    },
    click(event) {
      this.filterEvent(event, (event) => {
        if (window.playerElement) {
          window.playerElement.click(event);
        }
      });
    },
    onmousemove(event) {
      this.filterEvent(event, (event) => {
        if (window.playerElement) {
          window.playerElement.onmousemove(event);
        }
      });
    },
    onmousedown(event) {
      this.filterEvent(event, (event) => {
        if (window.playerElement) {
          window.playerElement.onmousedown(event);
        }
      });
    },
    onmouseup(event) {
      this.filterEvent(event, (event) => {
        if (window.playerElement) {
          window.playerElement.onmouseup(event);
        }
      });
    },
    onmousewheel(event) {
      this.filterEvent(event, (event) => {
        if (window.playerElement) {
          window.playerElement.onmousewheel(event);
        }
      });
    },
    onmouseenter(event) {
      this.filterEvent(event, (event) => {
        if (window.playerElement) {
          window.playerElement.onmouseenter(event);
        }
      });
    },
    onmouseleave(event) {
      this.filterEvent(event, (event) => {
        if (window.playerElement) {
          window.playerElement.onmouseleave(event);
        }
      });
    },
  },
  computed: {},
  watch: {},
};
</script>


